import React, { useState, useEffect } from 'react';
import { View, TextInput, TouchableOpacity, Text, StyleSheet } from 'react-native';
import { Wallet, useWallet } from '../hooks/useWallet';


const PasswordWallet = ({
  lockState,
  setLockState
}) => {
  // const queryClient = useQueryClient();
  const [inputPassword, setInputPassword] = useState('');
  const [password, setPassword] = useState('');

  const [privateKey, setPrivateKey] = useState('');
  const [submittedPassword, setSubmitted] = useState(false);
  const [submitTimestamp, setSubmitTimestamp] = useState(0);
  
  const { data: wallet, error, isError } = useWallet({
    password: password,
    enabledState: submittedPassword,
    timestamp: submitTimestamp
  });
  
  const handleUnlockWallet = () => {
    // setPrivateKey((wallet as Wallet).privateKeyEncrypted);
    setPassword(inputPassword);
    setSubmitted(true);
    setSubmitTimestamp(Date.now());
  };

  const handleLockWallet = () => {
    setPrivateKey('');

    setLockState(true);
    setSubmitted(false);
    setSubmitTimestamp(Date.now());
    setInputPassword('');
  };


  useEffect(() => {
    console.log('what is error', isError, error)
    console.log('what is wallet', wallet)
    let successful_fetch_wallet = (wallet && !isError)
    if (successful_fetch_wallet) {
      console.log('effect - UNLOCKING WALLET', wallet);

      // setPrivateKey((wallet as Wallet).privateKeyEncrypted);
      
      setLockState(false);
    } else {
      console.log('effect - LOCKING WALLET', wallet);
      
      // setPrivateKey('');
      
      setLockState(true);
    }
  }, [wallet]);
  
  
  return !lockState ? (
      <View style={styles.buttonsContainer}>
         <TouchableOpacity
          style={styles.submitButton}
        >
          <Text style={styles.getKeyButtonText}>Send</Text>
        </TouchableOpacity>
        <TouchableOpacity
          style={styles.submitButton}
        >
          <Text style={styles.getKeyButtonText}>Receive</Text>
        </TouchableOpacity>
        <TouchableOpacity
          style={styles.submitButton}
          onPress= {handleLockWallet}
        >
          <Text style={styles.getKeyButtonText}>Lock</Text>
        </TouchableOpacity>
      </View>
    ) : (
      <View>
        <TextInput
          style={styles.input}
          placeholder="Password"
          secureTextEntry={true}
          onChangeText={setInputPassword}
          value={inputPassword}
        />
        <TouchableOpacity
          style={styles.submitButton}
          onPress={handleUnlockWallet}
        >
          <Text style={styles.getKeyButtonText}>Unlock Wallet</Text>
        </TouchableOpacity>
        {!lockState ? null : (
          <Text style={styles.errorText}>{isError ? error.message : ''}</Text>
        )}
      </View>
  );
};

const borderColor = '#000';
const styles = StyleSheet.create({
  input: {
    borderWidth: 1,
    borderColor: borderColor,
    padding: 10,
    width: '100%',
    marginBottom: 10,
  },
  buttonsContainer: {
    flexDirection: 'row',
    justifyContent: 'space-evenly', // add more horizontal spacing
    alignItems: 'center', // center the buttons vertically
    marginHorizontal: 20 // add some horizontal margin to the container
  },
  submitButton: {
    backgroundColor: '#007BFF',
    padding: 10,
    alignItems: 'center',
    justifyContent: 'center',
    borderRadius: 5,
    marginHorizontal: 1, // add some horizontal margin to the buttons
    marginBottom: 5
  },
  getKeyButtonText: {
    color: '#fff',
    fontWeight: 'bold',
  },
  decryptedKeyText: {
    color: 'green',
    marginTop: 10,
  },
  errorText: {
    color: 'red',
    marginTop: 10,
  },
});

export default PasswordWallet;
